export const scheme = document.location!.protocol;

export const setResult = (id: string, message: string, errCode: string,) => {
    const dom = document.getElementById(id + '-result');
    if (dom) {
        dom.innerHTML = '';
        const span = document.createElement('span');
        let innnerHTML = '';
        if (errCode !== '') {
            span.className = 'uk-text-danger';
            innnerHTML = '[code]' + errCode + '\n[message]' + message;
        } else {
            span.className = 'uk-text-primary';
            innnerHTML = message;
        }
        span.innerHTML = innnerHTML;
        dom.appendChild(span);
    }
}

const initHttpButton = () => {
    const scheme = document.location!.protocol;
    const http11 = document.getElementById('http11');
    const http2 = document.getElementById('http2');

    alert(scheme);
    if (!http11 || !http2) {
        return;
    }

    if (scheme === 'http:') {
        http11.classList.add('uk-active');
        http2.classList.remove('uk-active');
    } else {
        http11.classList.remove('uk-active');
        http2.classList.add('uk-active');
    }
}
initHttpButton();